@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

:root {
  --primary-color: #6773b8;
  --black-bg: #333;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif !important;
}

a {
  text-decoration: none;
}

h2 {
  border-bottom-width: 5px;
  border-bottom-style: solid;
  border-image: linear-gradient(to right, rgba(185, 52, 52, 0.6),rgba(0, 0, 0, 0.6)) 25% 1;
}

.head {
  padding: 2rem 0;
  padding-bottom: 3rem;
}

i {
  padding: 0 10px;
}

form {
  width: 100%;
}

#svg {
  position: relative;
  height: 340px;
  width: 340px;
}

.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus {
    border: none;
    outline: none;
    box-shadow: none;
}

.about-me {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.animation {
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-contacts {
  display: flex;
  flex-direction: column;
}

.contacts {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 0;
}

.home-container {
  height: 95vh;
  min-height: 750px;
  background-image: url('https://i.imgur.com/Cr5Wg49.jpg'), linear-gradient(rgba(185, 52, 52, 0.6),rgba(0, 0, 0, 0.6));
  background-blend-mode: overlay;
  background-size: cover;
  box-shadow: inset 0 0 0 50vw rgba(0, 0, 0, 0.7);
}

.projects-container {
  min-height: 100vh;
  padding-bottom: 2rem;
}

.contact-container {
  height: 80vh;
  min-height: 600px;
  padding-bottom: 2rem;
}

.home {
  height: 95%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.img-container {
  width: 300px;
  height: 300px;
  background: var(--primary-color);
  border-radius: 50%;
}

.image {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.card {
  width: 32rem;
  border: none !important;
  box-shadow: 2px 2px 5px rgba(0,0,0,0.1),
              -2px -2px 5px rgba(0,0,0,0.1) !important;
  transition: all 0.3s ease;
}

.card:hover {
  box-shadow: 5px 5px 10px rgba(0,0,0,0.2),
              -5px -5px 10px rgba(0,0,0,0.2) !important;
}

.project-img {
  height: 30rem;
  object-fit: cover;
}

.link {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  background: var(--black-bg);
  border-radius: 4px;
  padding: 2px 6px;
}

.link:hover {
  color: white;
  opacity: 0.9;
  cursor: pointer;
}

.more-info {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.github-link {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tech {
  display: flex;
  gap: 5px;
}

.badge {
  background: #f5f4f4;
  color: var(--black-bg);
}

.max-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-fields {
  padding-bottom: 1rem;
}

input:active,
input:focus,
textarea:active,
textarea:focus {
  outline: none !important;
  -webkit-appearance:none;
  box-shadow: none !important;
  border: 1px solid #ddd !important;
}

.btn-contact {
  font-size: large;
  margin: 0 auto;
  padding: 6px 18px;
  outline: none;
  border: none;
}

.contact {
  transition: all 0.3s ease;
}

.contact:hover {
  transform: translateY(-2px);
}

@media only screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
  .project-img {
    height: 32rem;
  }
}

@media screen and (max-width: 1024px) { 
  .card {
    width: 100%;
  }
  .project-img {
    height: 32rem;
  }
}

@media screen and (min-width: 1920px) and (max-width: 3840px) {
  .card {
    width: 52rem;
  }
  .project-img {
    height: 50rem;
  }
}
